const validate = values => {
  const errors = {}
  const requiredFields = [
    'name',
    'manufacturer',
    'quantity',
    'type'
  ]
  requiredFields.forEach(field => {
    if (values[field] === null || values[field] === '' ) {
      errors[field] = 'Vui lng khng  trng'
    }
  })
  if ( parseInt(values.quantity) === 0 ) {
    errors.quantity = 'S lng ln hn 0'
  }
  // if ( parseInt(values.price) < 1000  ) {
  //   errors.price = 'Gi > 1.000 vn'
  // }
  // if ( parseInt(values.cash) < 1000 ) {
  //   errors.cash = 'Thnh tin > 1.000vn'
  // }
  return errors
}
export default validate;
